<template>
  <div id="FindTheNumber" class="mb-16">
    
    <div v-show="alert.status" :class="alert.class" class="fixed z-50 w-full flex justify-center alertFade" style="top:10%">
      <span :class="alert.color" class="px-10 py-2 rounded-lg font-bold text-black text-3xl" v-text="alert.msg" />
    </div>

    <div class="w-full flex flex-col items-center mt-16 font-bold text-sm lg:text-xl">
      <div class="w-full flex flex-wrap justify-center max-w-5xl items-center">
        <div class="">
          <button class='bg-purple-500 uppercase rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="changeSettings()">restart</button>
        </div>
      </div>
      <div class="w-full flex flex-wrap justify-between max-w-5xl mt-5 items-center">
        <div class="ml-10">Score : <span v-text="score" /></div>
        <div class="mr-10">Time Left : <span :class="getTimeColor()" v-text="timer"/> s</div>
      </div>
    </div>
    

    <div class="w-full flex flex-col items-center font-bold">
      <div class="bg-gray-500 w-1/6 h-12 sm:h-16 md:h-24 rounded-lg">
          <span class="text-4xl sm:text-5xl md:text-6xl" :class="targetNumber.color" v-text="targetNumber.num" />
      </div>
      <div class="w-full flex flex-wrap max-w-5xl mt-3">
        <div class="md:p-1 w-1/6 h-12 sm:h-16 md:h-20" v-for="(number, index) in numbersList" :key="index">

          <div v-bind:class="number.bgcolor" class="h-full w-full rounded-lg overflow-hidden cursor-pointer border" @click="clickNumber(number)">
            <span class="text-3xl sm:text-4xl md:text-5xl" :class="number.color" v-text="number.num" />
          </div>

        </div>
      </div>
    </div>

    <div class="w-full flex mt-10 justify-center">
      <div class="w-full flex flex-wrap justify-center max-w-5xl">
        <div class='w-1/3 px-3 mb-6'>
          <label class='block uppercase tracking-wide text-gray-700 text-sm font-bold mb-2'>Levels:</label>
          <div class='relative'>
            <select v-model="level" class='block appearance-none w-full bg-purple-300 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none'>
              <option v-for="(level,index) in levels" :value="index" :key="level">{{ level }}</option>
            </select>
            <div class='pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700'>
            <svg class='fill-current h-4 w-4' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'>
            <path d='M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z'/>
            </svg>
            </div>
          </div>
          <label class='block tracking-wide text-gray-600 text-xs mb-2'>in easy level you must found the same number</label>
          <label class='block tracking-wide text-gray-600 text-xs mb-2'>in hard level you must found the same number and same number color</label>
        </div>
      </div>
    </div>
  
  
    <span class="w-1/12 text-red-800 bg-red-500" />
    <span class="w-1/12 text-gray-800 bg-gray-500" />
    <span class="w-1/12 text-green-800 bg-green-500" />
    <span class="w-1/12 text-blue-800 bg-blue-500" />
    <span class="w-1/12 text-indigo-800 bg-indigo-500" />
    <span class="w-1/12 text-orange-800 bg-orange-500" />
    <span class="w-1/12 text-yellow-800 bg-yellow-500" />
    <span class="w-1/12 text-purple-800 bg-purple-500" />
    <span class="w-1/12 text-teal-800 bg-teal-500" />
    <span class="w-1/12 text-pink-800 bg-pink-500" />

  </div>
</template>

<script>
export default {
  name: 'FindTheNumber',
  components: {
  },
  data() {
    return {
      targetNumber:{num:0,color:"textt",bgcolor:"color"},
      numbersList:[],

      score:0,

      timeLimit: 60,
      timePassed: 0,
      timerInterval: null,
    
      stopGame:false,

      levels:["Easy","Hard"],
      level:0,
      levelNow:0,

      alert:{status:false,class:"opacity-0",color:null,msg:null},
      colors:["red","gray","green","blue","indigo","orange","yellow","purple","teal","pink"],
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.callAddNumber();
    },
    callAddNumber(){
      //this.AddNumbers();
      this.randomAddNumbers();
    },
    clickNumber(number){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.timerInterval) vm.startTimer();

      if(vm.targetNumber.num == number.num && vm.targetNumber.color == number.color){
        vm.score += 1;
        vm.alertShow("+1")
        vm.callAddNumber();
      }else{
        if(vm.score > 0)vm.score -= 1;
        if(vm.score > 0)vm.alertShow("-1","bg-red-500")
        else vm.alertShow("0","bg-red-500")
        vm.callAddNumber();
      }
    },
    AddNumbers(){
      var vm = this;
      vm.numbersList = [];
      
      for (let n = 0; n < 18; n++) {
        vm.numbersList.push({
          bgcolor:"bg-"+ vm.getRandomColor() +"-500",
          color:"text-gray-900",
          num:n
        });
      }

      vm.targetNumber = vm.numbersList[Math.floor(Math.random() * vm.numbersList.length)]
    },
    randomAddNumbers(){
      var vm=this;
      vm.numbersList = [];
      for(var a=[],i=0;i<42;++i)a[i]=Math.floor(i/3);

      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      
      for (let n = 0; n < a.length; n++) {
        vm.numbersList.push({
          bgcolor:"bg-"+ vm.getRandomColor() +"-500",
          color:vm.getColor(),
          num:a[n]
        });
      }

      vm.targetNumber = vm.numbersList[Math.floor(Math.random() * vm.numbersList.length)]
    },
    getColor(){
      var color = "";
      if(this.levelNow == 0) color = "gray";
      else color = this.getRandomColor();
      
      return "text-"+color+"-800";
    },
    getRandomColor(){
      var a=this.colors;
      var color = a[Math.floor(Math.random() * a.length)]
      return color;
    },

    getTimeColor(){
      var vm = this;
      if(vm.timePassed == vm.timeLimit)return 'text-red-500'
      else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
      else return 'text-green-500'
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(){
      clearInterval(this.timerInterval);
    },
    TimeOver(){
      this.stopGame = true;
      this.stopTimer();
      this.alertTimeOver();
      console.log("Time Over")
    },

    alertShow(msg,color = "bg-green-500"){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = color;
      vm.alert.msg = msg;
      setTimeout(function(){
        vm.alertFadeOut();
      },500);
    },
    alertTimeOver(){
      var vm = this;
      vm.alert.status = true;
      vm.alert.class = "opacity-1"
      vm.alert.color = "bg-red-500";
      vm.alert.msg = "Time Over :(";
      setTimeout(function(){
        vm.alertFadeOut();
      },6000);
    },
    alertFadeOut(){
      var vm = this;
      vm.alert.class = "opacity-0"
      setTimeout(function() {
        vm.alert.status = false;
      }, 500);
    },


    
    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.stopTimer();
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.levelNow = level;
      vm.MStart();
    }
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.TimeOver();
      return timeLeft
    }
  },
  watch: {
    level(){
      this.changeSettings();
    }
  },
}
</script>

<style>
.alertFade{
  -webkit-transition: opacity 0.5s ease-in-out;
  -moz-transition: opacity 0.5s ease-in-out;
  -ms-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
}
</style>
